import { Component, OnInit } from '@angular/core';
import {
  LoadingController,
  NavController,
  ToastController,
} from '@ionic/angular';
import { HttpClient } from '@angular/common/http';
import { BaseComp } from '../../utils/BaseComp';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.page.html',
  styleUrls: ['./signup.page.scss'],
})
export class SignupPage extends BaseComp implements OnInit {
  selected_farm: string;
  selected_cc: string;

  farmDetail_arr: FarmsDetails[] = [];
  collectionCenter_arr: CollectionCenter[] = [];

  first_name: string = '';
  middle_name: string = '';
  last_name: string = '';
  phone: string = '';
  pan: string = '';
  aadhar: string = '';
  cattle_cow: string = '';
  calves_cow: string = '';
  cattle_buffalo: string = '';
  calve_buffalo: string = '';
  cattle_desi: string = '';
  calves_desi: string = '';

  constructor(
    public navCtrl: NavController,
    private http: HttpClient,
    private loadingCtrl: LoadingController,
    private toastCtrl: ToastController
  ) {
    super();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignupPage');
    // this.getFarms() ;
  }

  ngOnInit() {}
  getFarms() {
    // var url = this.FARMER_DASHBOARD_URL + "?farmerDashboard=[{FarmerId:" + this.getItem(this.LOGIN_USER_ID) + ",Month:9,Year:2019,MilkType:0}]" ;
    var url =
      this.BASE_URL +
      'api/DairyFarmApi/getAllDairyFarm?UserId=2' +
      '&Key=' +
      this.key +
      '&con=' +
      this.DatabaseCode;
    // var url = this.LOGIN_URL+"?UserName="+this.email+"&Pword="+this.password ;

    let postData = new FormData();

    console.log('url:-' + url);

    this.http.get(url).subscribe(
      (result) => {
        this.farmDetail_arr = [];
        // loading.dismiss() ;
        try {
          // console.log("data:-" + JSON.stringify(result)) ;
          var parsedJSON = JSON.parse(JSON.stringify(result));
          for (let i = 0; i < parsedJSON.length; i++) {
            const farmDetail: FarmsDetails = {
              Farm_ID: parsedJSON[i].Farm_ID,
              Farm_Name: parsedJSON[i].Farm_Name,
              Comany_Name: parsedJSON[i].Comany_Name,
              Address: parsedJSON[i].Address,
              Zip_Code: parsedJSON[i].Zip_Code,
              City_ID: parsedJSON[i].City_ID,
              State_ID: parsedJSON[i].State_ID,
              Country_ID: parsedJSON[i].Country_ID,
              Owner_Name: parsedJSON[i].Owner_Name,
              Contact_Person: parsedJSON[i].Contact_Person,
            };
            this.farmDetail_arr.push(farmDetail);
            this.getCollectionCenters();
          }
        } catch (err) {
          console.log(err);
        }
      },
      (error) => {
        console.log('API Error', error);
        this.somethingWentWrongError(this.toastCtrl);
      }
    );
  }

  getCollectionCenters() {
    // var url = this.FARMER_DASHBOARD_URL + "?farmerDashboard=[{FarmerId:" + this.getItem(this.LOGIN_USER_ID) + ",Month:9,Year:2019,MilkType:0}]" ;
    var url =
      this.BASE_URL +
      'api/CollectionCentreApi/getAllCollectionCentre?UserId=2' +
      '&Key=' +
      this.key +
      '&con=' +
      this.DatabaseCode;
    // var url = this.LOGIN_URL+"?UserName="+this.email+"&Pword="+this.password ;

    let postData = new FormData();

    console.log('url:-' + url);

    this.http.get(url).subscribe(
      (result) => {
        this.collectionCenter_arr = [];
        // loading.dismiss() ;
        try {
          // console.log("data:-" + JSON.stringify(result)) ;
          var parsedJSON = JSON.parse(JSON.stringify(result));
          for (let i = 0; i < parsedJSON.length; i++) {
            const collectionCenter: CollectionCenter = {
              CollectionCentre_ID: parsedJSON[i].CollectionCentre_ID,
              CollectionCentre_Name: parsedJSON[i].CollectionCentre_Name,
              PCollectionCentre_ID: parsedJSON[i].PCollectionCentre_ID,
              CollectionCentre_Type: parsedJSON[i].CollectionCentre_Type,
              Farm_ID: parsedJSON[i].Farm_ID,
              Address: parsedJSON[i].Address,
              Zip_Code: parsedJSON[i].Zip_Code,
              City_ID: parsedJSON[i].City_ID,
              Owner_ID: parsedJSON[i].Owner_ID,
              SupervisorName: parsedJSON[i].SupervisorName,
              Modified_By: parsedJSON[i].Modified_By,
              Modified_On: parsedJSON[i].Modified_On,
              City_Name: parsedJSON[i].City_Name,
              Owner_Name: parsedJSON[i].Owner_Name,
            };
            this.collectionCenter_arr.push(collectionCenter);
          }
        } catch (err) {
          console.log(err);
        }
      },
      (error) => {
        console.log('API Error', error);
        this.somethingWentWrongError(this.toastCtrl);
      }
    );
  }

  makeSignupRequest() {
    let signUp = {
      FirstName: this.first_name,
      MiddleName: this.middle_name,
      LastName: this.last_name,
      Phone: this.phone,
      PAN: this.pan,
      RFID: '10',
      Aadhar: this.aadhar,
      Farm_ID: this.farmDetail_arr[Number(this.selected_farm)],
      CollectionCentre_ID: this.collectionCenter_arr[Number(this.selected_cc)],
      CattleCowNo: this.cattle_cow,
      CalvesCowNo: this.calves_cow,
      CattleBuffaloNo: this.cattle_buffalo,
      CalvesBuffaloNo: this.calve_buffalo,
      CattleDesiCowNo: this.cattle_desi,
      CalvesDesiCowNo: this.calves_desi,
    };
    let data = [];
    data.push(signUp);

    return JSON.stringify(data);
  }

  async callSignupAPI() {
    let loading = await this.loadingComp(this.loadingCtrl);
    loading.present();

    var url =
      this.BASE_URL +
      'api/FarmerApi/addFarmer?FarmerDetail=' +
      this.makeSignupRequest() +
      '&Key=' +
      this.key +
      '&con=' +
      this.DatabaseCode;
    // var url = this.LOGIN_URL+"?UserName="+this.email+"&Pword="+this.password ;

    let postData = new FormData();

    console.log('url:-' + url);

    this.http.post(url, postData).subscribe(
      (result) => {
        loading.dismiss();
        try {
          // console.log("data:-" + JSON.stringify(result)) ;
          if (JSON.stringify(result) == 'true') {
            this.presentToast(
              this.toastCtrl,
              'User Register! Please Login Now.'
            );
            this.navCtrl.pop();
          } else {
            this.presentToast(this.toastCtrl, 'Something went wrong');
          }
        } catch (err) {
          console.log(err);
        }
      },
      (error) => {
        loading.dismiss();
        console.log('API Error', error);
        this.somethingWentWrongError(this.toastCtrl);
      }
    );
  }

  backToLogin() {
    this.navCtrl.pop();
  }
}

interface FarmsDetails {
  Farm_ID: string;
  Farm_Name: string;
  Comany_Name: string;
  Address: string;
  Zip_Code: string;
  City_ID: string;
  State_ID: string;
  Country_ID: string;
  Owner_Name: string;
  Contact_Person: string;
}

interface CollectionCenter {
  CollectionCentre_ID: string;
  CollectionCentre_Name: string;
  PCollectionCentre_ID: string;
  CollectionCentre_Type: string;
  Farm_ID: string;
  Address: string;
  Zip_Code: string;
  City_ID: string;
  Owner_ID: string;
  SupervisorName: string;
  Modified_By: string;
  Modified_On: string;
  City_Name: string;
  Owner_Name: string;
}
